import * as React from 'react';
import { Helmet } from 'react-helmet';
import { Link } from 'gatsby';

import { Introduction } from '../components/Introduction.React';
import { PastClients } from '../components/PastClients.React';
import { Contact } from '../components/Contact.React';
import { Footer } from '../components/Footer.React';
import { Layout } from '../components/Layout.React';

// import * as styles from './Index.module.scss';
// import './index.scss';
import '../scss/global.scss';
import './privacy-policy.scss';

import socialMediaCard from '../static/img/social-media-card.jpg';
import { SiteMetaData } from '../types/SiteMetaData';
import { Button } from '../ui/buttons/Button.React';

interface PrivacyPolicyPageProps {
  data: {
    site: {
      siteMetadata: {
        privacyPolicy: SiteMetaData;
      },
    },
  };
}

export const privacyPolicyPageQuery = graphql`
  query PrivacyPolicyPageQuery {
    site {
      siteMetadata {
        privacyPolicy {
          name
          tagline
          description
          keywords
          author
          canonicalLink
        }
      }
    }
  }
`;

const BackButton = () => (
    <div className="back-button">
      <Link  to="/">
        <Button> ‚ù∞ Back to JupioLabs.com</Button>
      </Link>
    </div>
  );

export default class PrivacyPolicyPage extends React.Component<PrivacyPolicyPageProps, {}> {

  public render() {
    const {
      name,
      tagline,
      description,
      keywords,
      author,
      canonicalLink
    } = this.props.data.site.siteMetadata.privacyPolicy;
    return (
      <Layout className="privacy-policy">
        <Helmet>
            <meta charSet="utf-8" />
            <meta name="viewport" content="width=device-width, initial-scale=1.0" />

            <title>{name} | {tagline}</title>
            
            <link rel="canonical" href={canonicalLink} />
            <meta name="description" content={description}></meta>
            <meta name="keywords" content={keywords.join(',')} />
            <meta name="author" content={author} />
            
            <meta property="og:title" content={name} />
            <meta property="og:description" content={description} />
            <meta property="og:image" content={socialMediaCard} />
            <meta property="og:url" content={canonicalLink} />
        </Helmet>

        <section>
          <article>
            <BackButton />
            <h2>Privacy Policy of Jupio Media Inc</h2>

            <p>Jupio Media Inc operates the https://www.jupiolabs.com/ website, which provides the SERVICE.</p>

            <p>This page is used to inform website visitors regarding our policies with the collection, use, and disclosure of Personal Information if anyone decided to use our Service, the Jupio Labs website.</p>

            <p>If you choose to use our Service, then you agree to the collection and use of information in relation with this policy. The Personal Information that we collect are used for providing and improving the Service. We will not use or share your information with anyone except as described in this Privacy Policy.</p>

            <p>The terms used in this Privacy Policy have the same meanings as in our Terms and Conditions, which is accessible at https://www.jupiolabs.com/, unless otherwise defined in this Privacy Policy.</p>

            <h3>Information Collection and Use</h3>

            <p>For a better experience while using our Service, we may require you to provide us with certain personally identifiable information, including but not limited to your name, phone number, and postal address. The information that we collect will be used to contact or identify you.</p>

            <h3>Log Data</h3>

            <p>We want to inform you that whenever you visit our Service, we collect information that your browser sends to us that is called Log Data. This Log Data may include information such as your computer‚Äôs Internet Protocol ("IP") address, browser version, pages of our Service that you visit, the time and date of your visit, the time spent on those pages, and other statistics.</p>

            <h3>Cookies</h3>

            <p>Cookies are files with small amount of data that is commonly used an anonymous unique identifier. These are sent to your browser from the website that you visit and are stored on your computer‚Äôs hard drive.</p>

            <p>Our website uses these "cookies" to collection information and to improve our Service. You have the option to either accept or refuse these cookies, and know when a cookie is being sent to your computer. If you choose to refuse our cookies, you may not be able to use some portions of our Service.</p>

            <p>You can learn how to manage cookies on your web browser by following the <a href="https://privacypolicies.com/blog/browser-cookies-guide/">Browser Cookies Guide</a>.</p>

            <h3>Service Providers</h3>

            <p>We may employ third-party companies and individuals due to the following reasons:</p>

            <ul>
                <li>To facilitate our Service;</li>
                <li>To provide the Service on our behalf;</li>
                <li>To perform Service-related services; or</li>
                <li>To assist us in analyzing how our Service is used.</li>
            </ul>

            <p>We want to inform our Service users that these third parties have access to your Personal Information. The reason is to perform the tasks assigned to them on our behalf. However, they are obligated not to disclose or use the information for any other purpose.</p>

            <h3>Security</h3>

            <p>We value your trust in providing us your Personal Information, thus we are striving to use commercially acceptable means of protecting it. But remember that no method of transmission over the internet, or method of electronic storage is 100% secure and reliable, and we cannot guarantee its absolute security.</p>

            <h3>Links to Other Sites</h3>

            <p>Our Service may contain links to other sites. If you click on a third-party link, you will be directed to that site. Note that these external sites are not operated by us. Therefore, we strongly advise you to review the Privacy Policy of these websites. We have no control over, and assume no responsibility for the content, privacy policies, or practices of any third-party sites or services.</p>

            <p>Children‚Äôs Privacy</p>

            <p>Our Services do not address anyone under the age of 13. We do not knowingly collect personal identifiable information from children under 13. In the case we discover that a child under 13 has provided us with personal information, we immediately delete this from our servers. If you are a parent or guardian and you are aware that your child has provided us with personal information, please contact us so that we will be able to do necessary actions.</p>

            <h3>Changes to This Privacy Policy</h3>

            <p>We may update our Privacy Policy from time to time. Thus, we advise you to review this page periodically for any changes. We will notify you of any changes by posting the new Privacy Policy on this page. These changes are effective immediately, after they are posted on this page. <a href="https://gdprprivacypolicy.net">Our GDPR Privacy Policy</a> was generated from the Privacy Policy Template.</p>

            <h3>Contact Us</h3>

            <p>If you have any questions or suggestions about our Privacy Policy, do not hesitate to contact us at <a href="mailto: info@jupio.media">info@jupio.media</a>.</p>

            <BackButton />
          </article>
        </section>
      </Layout>
    );
  }
}
